import * as React from 'react';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimesCircle } from '@fortawesome/free-solid-svg-icons';
// import { InputLabel } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid } from '@material-ui/core';

const useStyles = makeStyles(() => ({
    selectedItems: {
        borderRadius: 20,
        fontSize: 13,
        backgroundColor: '#d1d4db',
        padding: '5px 10px'
    },
    xIcon: {
        fontSize: 16
    }
}));
console.log(useStyles);

interface IAddRoleBodyProps {
    onClose: () => void;
    onSave: (items) => void;
}

interface IAddRoleBodyState {
    selectedItems: any;
    comboArray: any;
}

export class AddRoleBody extends React.Component<IAddRoleBodyProps, IAddRoleBodyState> {
    constructor(props) {
        super(props);
        this.state = {
            selectedItems: [],
            comboArray: [],
        }
    }
    // const {} = props;
    // const classes = useStyles();
    // const [selectedItems, setSelectedItems] = React.useState<any>([]);
    componentDidMount() {
        this.setState({
            comboArray: [
                {
                    name: 'John Ernest Seraspi',
                    value: 'test',
                },
                {
                    name: 'Teqpwoe',
                    value: 'test2',
                },
            ]
        })
    }

    handleOnSearch = (list) => {
        let arr = this.state.selectedItems;
        arr.push(list);
        this.setState({
            selectedItems: arr
        })
        console.log(arr);
    }

    handleRemoveItem = (index) => {
        let arr = this.state.selectedItems;
        arr.splice(index, 1);
        this.setState({
            selectedItems: arr
        })
    }

    handleOnSaveItems = () => {
        this.props.onSave(this.state.selectedItems);
    }

    public render(): JSX.Element {
        const classes = {
            buttonsContainer: {
                padding: '1em',
            },
            selectedContainer: {
                padding: '3em 0 2em'
            },
            selectedItems: {
                borderRadius: 20,
                fontSize: 13,
                backgroundColor: '#d1d4db',
                padding: '5px 10px',
                margin: '5px',
                display: 'inline-block',
            },
            selectedText: {
                paddingRight: '5px'
            },
            saveButton: {
                width: '135px',
                fontSize: '14px',
                color: '#fff',
            },
            cancelButton: {
                width: '135px',
                fontSize: '14px',
            }
        }; 
        
        return (
            <>
                <ReactSearch 
                    searchLabel={'Search user account'} 
                    onSearch={this.handleOnSearch}
                    comboBoxMode={true}
                    comboListsArray={this.state.comboArray}
                />
                <div style={classes.selectedContainer}>
                    <div>
                        <label>Selected Users</label>
                    </div>
                    { this.state.selectedItems.map((item,index) => 
                                <div style={classes.selectedItems}>
                                    <span style={classes.selectedText}>{item}</span>
                                    <span onClick={() => this.handleRemoveItem(index)}>
                                        <FontAwesomeIcon icon={faTimesCircle}/>
                                    </span>
                                </div>
                            ) 
                        }
                </div>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        <Button style={classes.cancelButton} variant="outlined" color="secondary" onClick={() => this.props.onClose()}>Cancel</Button>
                    </Grid>
                    <Grid item xs={6}>
                        <Button disabled={this.state.selectedItems.length < 1 ? true : false} style={classes.saveButton} variant="contained" color="primary" onClick={() => this.handleOnSaveItems()}>Save</Button> 
                    </Grid>
                </Grid>
            </>
        )
    }

}
